import MainTitle from '~/components/shack/MainTitle.vue'
import SearchFilter from '~/components/shack/SearchFilter.vue'

export default {
  layout: 'ShackDash',
  components: {
    MainTitle,
    SearchFilter
  },
  data: () => ({
    loading: false,
    data: null,
    card: null,
    cards: [],
    perPage: 10,
    items: [],
    query: '',
    currentPage: 1,
    dropdown: 30,
    days: [30, 15, 5],
    fields: ['full_name', 'plan', 'start_date', 'end_date', 'status', 'options']
  }),
  computed: {
    rows() {
      return this.items.length
    },
    filterMembersByName: function() {
      return this.items.filter(item =>
        item.full_name.match(new RegExp(this.query, 'i'))
      )
    }
  },
  watch: {
    '$route.query': '$fetch'
  },
  async asyncData({ error, $membership, route }) {
    if (route.query.plan) {
      return await $membership
        .subscribedToPlan(route.query.plan)
        .then(data => {
          let items = _.map(data, o => {
            return {
              id: o.id,
              full_name: `${o.user.first_name} ${o.user.last_name}`,
              plan: o.plan.name,
              plan_id: o.plan.id,
              slug: o.slug,
              start_date: o.starts_at,
              end_date: o.ends_at,
              status:
                o.state == 'manual-active'
                  ? 'Do not auto renew'
                  : o.state == 'active'
                    ? 'Auto-renewal'
                    : 'payment declined',
              membership_id: o.user.uuid
            }
          })
          return {
            data,
            items,
            viewing_plan_susbcriptions: true,
            plan_name: route.query.name
          }
        })
        .catch(e => {
          const message = e.response
            ? `${e.response.data.message} ~ ${JSON.stringify(
                e.response.data.errors
              )}`
            : e.message
          error({ statusCode: e.statusCode, message })
        })
    }
    return await $membership
      .expiringSubscriptions(30)
      .then(data => {
        let items = _.map(data, o => {
          return {
            id: o.id,
            full_name: `${o.user.first_name} ${o.user.last_name}`,
            plan: o.plan.name,
            plan_id: o.plan.id,
            slug: o.slug,
            start_date: o.starts_at,
            end_date: o.ends_at,
            status:
              o.state == 'manual-active'
                ? 'Do not auto renew'
                : o.state == 'active'
                  ? 'Auto-renewal'
                  : 'payment declined',
            membership_id: o.user.uuid
          }
        })
        return {
          data,
          items
        }
      })
      .catch(e => {
        const message = e.response
          ? `${e.response.data.message} ~ ${JSON.stringify(
              e.response.data.errors
            )}`
          : e.message
        error({ statusCode: e.statusCode, message })
      })
  },
  methods: {
    handleClick(day) {
      switch (day) {
        case -1:
          this.dropdown = '(Expired Auto-Renewals) 0'
          break
        case -2:
          this.dropdown = '(Unexpired Manual-Renewals) 0'
          break
        case -3:
          this.dropdown = '(Payment Declined) 0'
          break
        case -4:
          this.dropdown = '(Expired Manual-Renewals) 0'
          break
        default:
          this.dropdown = day
          break
      }

      this.loading = !this.loading

      this.$membership
        .expiringSubscriptions(day)
        .then(data => {
          this.loading = !this.loading

          this.items = _.map(data, o => {
            return {
              id: o.id,
              full_name: `${o.user.first_name} ${o.user.last_name}`,
              plan: o.plan.name,
              plan_id: o.plan.id,
              slug: o.slug,
              start_date: o.starts_at,
              end_date: o.ends_at,
              status:
                o.state == 'manual-active'
                  ? 'Do not auto renew'
                  : o.state == 'active'
                    ? 'Auto-renewal'
                    : o.state == 'payment-declined'
                      ? 'payment declined'
                      : 'expired',
              membership_id: o.user.uuid
            }
          })
        })
        .catch(e => {
          this.loading = !this.loading

          const message = e.response
            ? `${e.response.data.message} ~ ${JSON.stringify(
                e.response.data.errors
              )}`
            : e.message
          error({ statusCode: e.statusCode, message })
        })
    },
    showForm(e) {
      this.data = e
      this.$bvModal.show('renew')

      this.$membership
        .getPaymentMethods(e.item.membership_id)
        .then(({ data }) => {
          this.cards = _.map(data, o => {
            return {
              value: o.id,
              text: `${o.card_brand} - **** ${o.last_4}`
            }
          })
        })
        .catch(e => {
          const message = e.response
            ? `${e.response.data.message} ~ ${JSON.stringify(
                e.response.data.errors
              )}`
            : e.message

          this.$bvToast.toast(message, {
            variant: 'danger',
            title: 'Error'
          })
        })
    },
    beginRenew() {
      const payload = {
        plan_id: this.data.item.plan_id,
        card_nonce: this.card,
        slug: this.data.item.slug
      }

      this.renewSubscription(this.data.item.membership_id, payload)
    },
    renewSubscription(id, payload) {
      this.$membership
        .renewSubscription(id, payload)
        .then(res => {
          if (res) {
            this.items.splice(this.data.index, 1)
            this.$bvToast.toast(
              `${res.data.first_name} ${
                res.data.last_name
              }'s subscription renewed successfully`,
              {
                variant: 'success',
                title: 'Success'
              }
            )
            this.$bvModal.hide('renew')
            location.reload()
          }
        })
        .catch(e => {
          const message = e.response
            ? `${e.response.data.message} ~ ${JSON.stringify(
                e.response.data.errors
              )}`
            : e.message

          this.$bvToast.toast(message, {
            variant: 'danger',
            title: 'Error'
          })
        })
    },
    search(query) {
      this.loading = !this.loading

      this.items = this.items.filter(item =>
        item.full_name.match(new RegExp(query, 'i'))
      )
      console.log(this.items)

      this.loading = false

      //const link = `filter[status]=invited&include=primaryPlan,profile&filter[search]=${query}`
      // this.$membership
      //   .getAllMemberships(link)
      //   .then(({ data, links, meta }) => {
      //     this.members = data
      //     this.links = links
      //     //this.meta = meta

      //     this.loading = false
      //   })
      //   .catch(e => {
      //     this.loading = !this.loading
      //     this.$bvToast.toast(
      //       e.response ? JSON.stringify(e.response.data.errors) : e.message,
      //       {
      //         title: 'Error',
      //         variant: 'danger'
      //       }
      //     )
      //   })
    }
  }
}
